<template>
  <div id="zhexian" ref="myChart">
  </div>
</template>

<script>
import * as echarts from "echarts";
export default {
  data(){
    return{
      datas:[],
      name:[],
      heat:[],
      date:{'value1':this.msg},
      dated:[]
    }
  },
  props:['msg'],
  methods:{
  },
  created() {
    this.date.value1=this.msg
    this.$http.post('http://127.0.0.1:8000/api/lineChart_view/',this.date).then((res)=>{
      this.datas=res.data;
      for (var i=0;i<this.datas.length;i++){
        this.name[i]=this.datas[i].name
        this.heat[i]=this.datas[i].heat
        this.dated=this.datas[i].dated
      }
    });
    console.log(this.name)

  },
  mounted() {
    let chart = echarts.init(this.$refs.myChart);
    var that = this; //保存this
    setInterval( () => {
      let option = {
        title: {
          text: 'Temperature Change in the Coming Week'
        },
        tooltip: {
          trigger: 'axis'
        },
        legend: {},
        toolbox: {
          show: true,
          feature: {
            dataZoom: {
              yAxisIndex: 'none'
            },
            dataView: { readOnly: false },
            magicType: { type: ['line', 'bar'] },
            restore: {},
            saveAsImage: {}
          }
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
        },
        yAxis: {
          type: 'value',
          axisLabel: {
            formatter: '{value} °C'
          }
        },
        series: [
          {
            name: 'Highest',
            type: 'line',
            data: [10, 11, 13, 11, 12, 12, 9],
            markPoint: {
              data: [
                { type: 'max', name: 'Max' },
                { type: 'min', name: 'Min' }
              ]
            },
            markLine: {
              data: [{ type: 'average', name: 'Avg' }]
            }
          },
          {
            name: 'Lowest',
            type: 'line',
            data: [1, -2, 2, 5, 3, 2, 0],
            markPoint: {
              data: [{ name: '周最低', value: -2, xAxis: 1, yAxis: -1.5 }]
            },
            markLine: {
              data: [
                { type: 'average', name: 'Avg' },
                [
                  {
                    symbol: 'none',
                    x: '90%',
                    yAxis: 'max'
                  },
                  {
                    symbol: 'circle',
                    label: {
                      position: 'start',
                      formatter: 'Max'
                    },
                    type: 'max',
                    name: '最高点'
                  }
                ]
              ]
            }
          }
        ]
      };
      chart.setOption(option);
    },1000)
  }
}
</script>

<style scoped>
#zhexian{
  width: 340px;
  height: 340px;
}
</style>



