<template>
  <div id="zhu" ref="myChart">
  </div>

</template>

<script>
import * as echarts from "echarts";

export default {
  data(){
    return{
      datas:[],
      name:[1,2],
      heat:[100,200],
      date:{'value1':this.msg},
      child:'child',
    }
  },
  props:['msg'],
  methods:{
  },
  created() {
    // this.date.value1=this.msg
    //
    // this.$http.post('http://127.0.0.1:8000/api/histogram/',this.date).then((res)=>{
    //   this.datas=res.data;
    //   for (var i=0;i<this.datas.length;i++){
    //     this.name[i]=this.datas[i].name
    //     this.heat[i]=this.datas[i].heat
    //   }
    // });
  },
  mounted() {

    let chart = echarts.init(this.$refs.myChart);
    var that = this; //保存this
    setInterval( () => {
      let sourcee=[]
      for(var i=0;i<2;i++){
        sourcee.push(
          [this.name[i],this.heat[i]]
        )
      }
      let option = {
        dataset: [
          {
            dimensions: ['name','heat'],
            source: this.sourcee=sourcee
          },
          {
            transform: {
              type: 'sort',
              config: { dimension: 'heat', order: 'desc' }
            }
          }
        ],
        xAxis: {
          type: 'category',
          axisLabel: { interval: 0, rotate: 30 }
        },
        yAxis: {},
        series: {
          type: 'bar',
          encode: { x: 'name', y: 'score' },
          datasetIndex: 1
        }
      };
      chart.setOption(option);
    }, 1000)

      console.log(this.date);

  }
}
</script>

<style scoped>
#zhu{
  width: 340px;
  height: 340px;
}
</style>



