<template>
  <div>
    <div class="chart">
      <el-table
        :data="tableData"
        height="320"
        style="width: 100%">
        <el-table-column
          prop="ranking"
          label="排行"
          width="90">
        </el-table-column>
        <el-table-column
          prop="name"
          label="模型"
          width="90">
        </el-table-column>
        <el-table-column
          prop="comment"
          label="准确率"
          width="90">
        </el-table-column>
        <el-table-column
          prop="handle"
          label="操作"
          width="120">
          <template slot-scope="scope">
            <el-button
              @click="handleClick(scope.row)"
              type="text"
              size="small">
              保存
            </el-button>

          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import bus from "./bus";
export default {
  props:['msg'],
  methods: {
    position(row){
       bus.$emit('toBrother',this.value[row.ranking-1])
    },
    handleClick(row) {
      console.log(row.name);
      var _this = this
        axios({
          method:"post",
          headers:{
            'Content-Type':'application/json',
          },
          data:JSON.stringify(row.name),
          url:"http://127.0.0.1:8000/api/user_collect/"
        }).then((res)=>{
         if (res.data.Msg=='1')
         {
           this.$message({
                showClose: true,
                message: '收藏成功！',
                type: 'success'
              });
         }
         if (res.data.Msg=='2')
         {
           this.$message.error('已有此景点收藏记录！');
         }
      })
    }
  },
  created() {
    this.date.value1=this.msg
      var _this = this
        axios({
          method:"post",
          headers:{
            'Content-Type':'application/json',
          },
          data:JSON.stringify(this.date),
          url:"http://127.0.0.1:8000/api/recommendation/"
        }).then((res)=>{
         _this.tableData=res.data;
         for (var i=0;i<this.tableData.length;i++){
           this.value[i]=this.tableData[i].value
         }
      })
  },
  data(){
    return{
      date:{'value1':this.msg},
      tableData: [],
      value:[]
    }
  }

}
</script>

<style>
</style>



