<template>
  <div style="background-color: white">
<el-header>
   <el-button class="out" @click="back" type="success" icon="el-icon-caret-left" >返回</el-button>
  <el-button class="out" @click="out" type="danger" icon="el-icon-back" >退出</el-button>
</el-header>
    <el-container style="height: 800px; border: 1px solid #eee;">

      <el-aside width="300px" style="margin-top: 1%" >
        <div style="width: 190px;background-color: white;padding: 5px;margin: auto">
          <img src="../assets/login.png" width="150px" height="150px" style="border-radius: 300px;margin-left: 8%">
          <br>
          <br>
          <p style="margin:0 auto;text-align: center" v-model="user.username">{{user.username}}</p>
        </div>

        <br>
        <br>
        <el-menu :default-openeds="['1','2']">
          <el-submenu index="1">
            <template slot="title"><i class="el-icon-office-building"></i>历史记录</template>
            <el-menu-item-group>

              <router-link class="alink" to="/userCenter/collect"><el-menu-item index="1-1">收藏景点</el-menu-item></router-link>
              <router-link class="alink" to="/userCenter/evaluate"><el-menu-item index="1-2">历史评价</el-menu-item></router-link>
            </el-menu-item-group>

          </el-submenu>
          <el-submenu index="2">
            <template slot="title"><i class="el-icon-s-custom"></i>个人中心</template>
            <el-menu-item-group>
              <router-link class="alink" to="/userCenter/modify"><el-menu-item index="2-1">信息修改</el-menu-item></router-link>
              <router-link class="alink" to="/userCenter/back"><el-menu-item index="2-2">评价反馈</el-menu-item></router-link>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-aside>


      <el-container style="background-color: white">
        <el-main>
          <router-view/>
        </el-main>
      </el-container>

    </el-container>

  </div>
</template>

<script>

import axios from "axios";

export default {
  name: "Space",
    created() {
    var _this = this
        axios({
          method:"get",
          headers:{
            'Content-Type':'application/json',
          },
          url:"http://127.0.0.1:8000/api/get_cookie/"
        }).then((res)=>{
         _this.user.username=res.data;
      })
  },
  data() {
    return {
      user:{
        userpic :"static/userpic/userpic.jpg",
        username: '用户'
      }
    }
  },
  methods:{
    out(){
      axios({
          method:"get",
          headers:{
            'Content-Type':'application/json',
          },
          url:"http://127.0.0.1:8000/api/exited/"
        }).then((res)=>{
      })
      this.$router.push("/login");
    },
    back(){
      this.$router.push("/user");
    }
  }

}
</script>

<style scoped>
.link{
  margin-left: -1640px;
}
.el-header {
  background-color: lightsteelblue;
  color: #3498db;
  line-height: 60px;
}

.el-aside {
  color: #333;
}
.alink{
  text-decoration: none;
}
.out{
  float: left;
  margin-top: 10px;
}
</style>




