<template>
  <el-form style="margin-top:180px" label-width="10px" :label-position="labelPosition" ref="LoginFormRef" :model="user" class="login_form">
    <el-form-item  label="反馈" prop="back">
      <el-input style="width: 500px" placeholder="反馈" v-model="back" prefix-icon="el-icon-user"></el-input>
    </el-form-item>
    <el-form-item class="btns">
      <el-button type="primary" @click="submit">提交</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      labelPosition: 'top',
      back:''
    }
  },
  methods:{
    submit(){
        axios({
          method:"post",
          headers:{
            'Content-Type':'application/json',
          },
          data:JSON.stringify(this.back),
          url:"http://127.0.0.1:8000/api/back/"
        }).then(res => {
        })
      this.$message({
                message: '反馈成功！',
                type: 'success'
              });
    }
  }
}
</script>



