<template>
  <div class="login_container">
    <div class="login_box">
      <!-- 头像 -->
      <div class="avatar_box">
        <img src="../assets/login.png" alt="">
      </div>
      <el-tabs class="tabs" v-model="activeName">
        <el-tab-pane label="登录" name="first">
          <!-- 表单 -->
          <el-form ref="LoginFormRef" :model="loginForm" label-width="0" :rules="LoginFormRules" class="login_form">
            <!--登录-->
            <el-form-item prop="username">
              <!-- 用户名-->
              <el-input  placeholder="用户名" v-model="loginForm.username" prefix-icon="el-icon-user"></el-input>
            </el-form-item>
            <!-- 密码-->
            <el-form-item prop="password">
              <el-input placeholder="密码" v-model="loginForm.password" prefix-icon="el-icon-lock" type="password"></el-input>
            </el-form-item>
            <el-form-item class="btns">
              <el-button type="primary" @click="login">登录</el-button>
              <el-button type="info" @click="resetLoginForm">重置</el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="注册" name="second">
          <!--            注册组件-->
          <register></register>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
//引入注册组件
import register from "./Register";
import axios from "axios";
export default {
  data() {
    return {
      activeName: 'first',
      // 登录的初始化数据：备注默认应该是空
      loginForm:{
        identity:'用户',
        username:'',
        password:''
      },
      // 正则表达式的相关校验
      LoginFormRules:{
        username:[
          { required: true, message: '请输入用户名', trigger: 'blur' },
        ],
        password:[
          { required: true, message: '请输入密码', trigger: 'blur' },
        ]
      }
    }
  },
  methods: {
    // 清空表单的校验
    resetLoginForm() {
      this.$refs.LoginFormRef.resetFields()
    },
    //登录的方法：登录逻辑写里面

    login() {
      if (this.loginForm.username != '' && this.loginForm.password != '') {
          axios({
            method:"post",
            headers:{
              'Content-Type':'application/json',
            },
            data:JSON.stringify(this.loginForm),
            url:"/login"
          }).then(res => {
            if (res.data=='0')
              this.$message.error('密码错误，登陆失败！');
            else
            {
              this.$message({
                message: '登录成功！',
                type: 'success'
              });
              this.$router.push("/user");

            }
          })
        }
        else
          this.$message({
            message: '输入不能为空！',
            type: 'warning'
          });

      }
    },
    components: {
      register
    }
}
</script>

<style lang="less" scoped>
.login_container{
  background-color: rgba(28, 21, 242, 0.25);
  height: 100%;
}
.login_box{
  width: 450px;
  height: 360px;
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  box-shadow: 0 0 10px #ddd;
}

.id_box{
  height: 50px;
  letter-spacing: 15px;
}

.avatar_box{
  width: 130px;
  height: 130px;
  border: 1px solid #eee;
  border-radius: 50%;
  padding: 10px;
  box-shadow: 0 0 10px #ddd;
  position: absolute;
  left:50%;
  transform: translate(-50%,-50%);
  background-color: #fff;
  img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #eee;
  }
}

.login_form{

  bottom: 0;
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
}

.btns{
  justify-content:flex-end;
  height: 25px;
  letter-spacing: 50px;
}

.register{
  margin-left: 25px;
  height: 30px;
}

.tabs{
  padding-top: 45px;
  padding-left: 12px;
}

</style>>



