import Vue from 'vue'
import Router from 'vue-router'
import Login from "../components/Login";
import Register from "../components/Register";
import UserCenter from "../components/UserCenter";
import Collect from "../components/user/collect";
import Evaluate from "../components/user/evaluate";
import Modify from "../components/user/modify";
import User from "../components/User";
import Back from "../components/user/back";
import User3 from "../components/User3"
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/Login'
    },
    {
      path: '/user3',
      component: User3
    },
    {
      path: '/user',
      component: User
    },
    {
      path: '/register',
      component: Register
    },
    {
      path: '/login',
      component: Login
    },
    {
      path: '/userCenter',
      component: UserCenter,
      children:[
        {
          path: 'collect',
          component: Collect
        },
        {
          path: 'evaluate',
          component: Evaluate
        },
        {
          path: 'modify',
          component: Modify
        },
        {
          path: 'back',
          component: Back
        }
      ]
    }
  ]
})



