<template>
  <el-table
    :data="tableData_eva"
    style="width: 100%">
    <el-table-column
      prop="scenry"
      label="景点名"
      width="180">
    </el-table-column>
    <el-table-column
      prop="comment"
      label="评价"
      width="180">
    </el-table-column>
  </el-table>
</template>

<script>
import axios from "axios";

export default {
  created() {
    var _this = this
        axios({
          method:"get",
          headers:{
            'Content-Type':'application/json',
          },
          url:"http://127.0.0.1:8000/api/comments_view/"
        }).then((res)=>{
         _this.tableData_eva=res.data;
      })
  },
  data() {
    return {
      tableData_eva: []
    }
  }
}
</script>



